import React, { Component } from 'react'
import {connect} from 'dva'
import {List, Divider, Button, Slider, Modal, Tag} from 'antd'
// import UtilGlobal from '../../utils/global'
import {getUrlParams} from '@/utils/utils'
import ReactMarkdown from 'react-markdown'

import BaseContent from '@/components/BaseContent'

import styles from './paper.module.less'
// const {basePath} = UtilGlobal;
const confirm = Modal.confirm;

class QuestionsList extends Component {
  render () {
    const {list} = this.props

    return (
      <List 
        className="demo-loadmore-list"
        itemLayout="horizontal"
        dataSource={list}
        renderItem={(item, index) => (
          <List.Item>
              <List.Item.Meta
                title={
                  <span className={styles.questionsTitle}>{index+1}，{item.title} <Tag color="blue">{item.questions_type_text}</Tag></span>
                }
                description={
                  <div className={styles.questionsList}>
                    <ReactMarkdown className="react-markdown" source={item.questions_stem} />
                    <div className={styles.answer}>
                      <div style={{width:0, flex:1, marginRight: 20}}>
                        <Divider orientation="left">学生答案</Divider>
                        <ReactMarkdown className="react-markdown" source={item.student_answer} />
                      </div>
                      <div style={{width:0, flex:1, marginLeft: 20}}>
                        <Divider orientation="left">标准答案</Divider>
                        <ReactMarkdown className="react-markdown" source={item.questions_answer} />
                      </div>
                    </div>
                  </div>
                  
                }
              />
              
          </List.Item>
        )}
      />
    )
  }
}

const mapStateToProps = store=>{
    return {...store.examValueate, ...store.main}
}
const mapDispatchToProps = dispatch => {
  return {
    initData () {
      const {paper_id} = getUrlParams()
      console.log(paper_id)
      dispatch({
        type: 'examValueate/studentPaper',
        paper_id
      })
    },
    correction (score, paper_id) {
      return dispatch({type: 'examValueate/correction', score, paper_id})
    }
  }
}

@connect(mapStateToProps,mapDispatchToProps)
class ClassList extends Component {
  state = {
    score: 0
  }

  componentWillReceiveProps (nextProps) {
    if (!this.state.score) {
      this.setState({
        score: nextProps.paper_detal.score
      })
    }
  }

  handlerClick = () => {
    const {score} = this.state
    const {paper_id} = getUrlParams()
    confirm({
      title: '确定提交阅卷结果？',
      content: `分数值是${score}`,
      onOk: () => {
        const {student_name} = this.props.paper_detal
        this.props.correction(score, paper_id).then(() => {
          console.log('成功')
          Modal.success({
            title: '批卷结果',
            content: `批改试卷成功  ${student_name}得分${score}`,
            onOk: () => {
              this.props.history.goBack()
            }
          });
        })
      },
      onCancel() {
        console.log('Cancel');
      },
    })
  }

  componentDidMount () {
    this.props.initData()
  }

  render() {
    const {questions, student_name} = this.props.paper_detal
    console.log(this.props.paper_detal)
    return (
      <div className={styles.content}>
        <BaseContent>
          <QuestionsList list={questions} />
        </BaseContent>
        <div style={{width: 300, flexShrink: 0, marginLeft: 20}}>
          <div style={{width: 300, position: 'fixed', top: 155}}>
          <BaseContent style={{width: 300}}>
            <div>
              <p></p>
              <h2>{student_name}</h2>
              <h2>得分: <span style={{fontSize: 28, color: 'rgba(1,57,253,1)'}}>{this.state.score}</span></h2>
              <Slider value={this.state.score}  style={{padding: '20px 0'}} onChange={(value) => this.setState({score: value})}/>
              <Button type="primary" onClick={this.handlerClick}>确定</Button>
            </div>
          </BaseContent>
          </div>
        </div>
      </div>
    )
  }
}
export default ClassList
